import React from 'react'
import PropTypes from 'prop-types'
import { connectHits } from 'react-instantsearch-dom'

const NumberedHits = ({ hits, hitComponent: HitComponent }) => hits
  .map((hit, index) => <HitComponent key={hit.objectID} hit={hit} hitNumber={index} />)

NumberedHits.propTypes = {
  hits: PropTypes.array.isRequired,
  hitComponent: PropTypes.func.isRequired
}

export default connectHits(NumberedHits)



