import { connect } from 'react-redux'
import { submit, isValid } from 'redux-form'
import { withRouter } from 'react-router-dom'
import SaveJobAlertsButton from 'Candidate/components/SaveJobAlertsButton'
import { getUrlToSearchState } from 'Shared/selectors/url'
import { getQueryValue } from 'Shared/helpers/Algolia'
import { formName } from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'
import { getUserRole } from 'Shared/selectors/user'
import * as R from 'ramda'

const mapStateToProps = (state, ownProps) => {
  const disableSaveJobAlertButton = R.pipe(
    getUrlToSearchState,
    getQueryValue,
    R.values,
    R.isEmpty
  )(ownProps.location.search, true)

  return ({
    disableSaveJobAlertButton,
    canCloseModal: isValid(formName)(state),
    userRole: getUserRole(state)
  })
}

const mapDispatchToProps = dispatch => ({
  submitForm: () => dispatch(submit(formName))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SaveJobAlertsButton))



