import React, { PureComponent } from 'react'
import { withRouter } from 'react-router-dom'
import JobOfferSearchListConnector from 'Candidate/connectors/Views/JobOfferSearchListConnector'
import JobsEmptyStateListConnector from 'Candidate/connectors/Views/JobsEmptyStateListConnector'
import withCandidateUnbouncedPopup from 'Candidate/hoc/withCandidateUnbouncedPopup'
import CustomJobSearchService from 'Shared/services/CustomJobSearchService'
import qs from 'query-string'
import './JobsView.scss'

const customJobSearchService = new CustomJobSearchService()

const notQueryWords = ['in', 'summer', 'jobs', 'fresh', 'recent']

class JobsView extends PureComponent {
  state = {
    searchResults: null,
    jobSearchData: {}
  }

  componentWillMount () {
    if (this.props.searchQuery) {
      const isUnwantedQueryWord = word => notQueryWords.indexOf(word) < 0
      const preparedSearchQuery = this.props.searchQuery.split(' ').filter(isUnwantedQueryWord).join(' ')
      this.props.history.push(`?query=${preparedSearchQuery}`)
    }
  }

  async componentDidMount () {
    if (this.props.match.params.urlString) {
      const hash = qs.parse(this.props.location.search).hash
      const { data } = await customJobSearchService.fetchCustomJobsSearch(this.props.match.params.urlString, hash)
      this.setState({ jobSearchData: data })
    }
  }

  setSearchResults = searchResults => this.setState({ searchResults })

  resetJobSearchData = () => this.setState({ jobSearchData: {} })

  render () {
    const { searchQuery, isMobile } = this.props
    return (
      <div className='jobs-view__container parsing--hidden'>
        <JobOfferSearchListConnector
          isMobile={isMobile}
          resetJobSearchData={this.resetJobSearchData}
          jobSearchData={this.state.jobSearchData}
          searchQueryFromUrl={searchQuery}
          searchResults={this.state.searchResults}
          setSearchResults={this.setSearchResults}
        />
        { this.state.searchResults === 0 && <JobsEmptyStateListConnector isMobile={isMobile} /> }
      </div>
    )
  }
}

export default withRouter(withCandidateUnbouncedPopup(JobsView))



