import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connectStats } from 'react-instantsearch-dom'
import withTranslations from 'Shared/hoc/withTranslations/index'
import './SearchResults.mobile.scss'

class SearchResults extends Component {
  shouldComponentUpdate (nextProps) {
    return nextProps.nbHits !== this.props.nbHits
  }

  componentDidMount () {
    this.props.setSearchResults && this.props.setSearchResults(this.props.nbHits)
  }

  componentDidUpdate (prevProps) {
    if (prevProps.nbHits !== this.props.nbHits) {
      this.props.setSearchResults && this.props.setSearchResults(this.props.nbHits)
    }
  }

  render () {
    const { t } = this.props

    return this.props.isDefaultSearch ? null : (
      <div className='search-results-mobile__container'>
        {`${t('common.searchResults')} (${this.props.nbHits})`}
      </div>
    )
  }
}

SearchResults.propTypes = {
  generateLabel: PropTypes.func
}

export default withTranslations(connectStats(SearchResults))



