import RadioSelectRefinementListWeb from './RadioSelectRefinementList'
import RadioSelectRefinementListMobile from './RadioSelectRefinementList.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import React from 'react'

const RadioSelectRefinementList = props => props.isMobile ? <RadioSelectRefinementListMobile {...props} /> : <RadioSelectRefinementListWeb {...props} />

export default withDeviceWidthDetector(RadioSelectRefinementList)



