import React, { PureComponent } from 'react'
import { compose } from 'redux'
import JobOfferCardAlgoliaMobileConnector from 'Candidate/connectors/JobOfferCardAlgolia'
import JobsSearchFiltersMobileConnector from 'Candidate/connectors/JobsSearchFilters/JobSearchFiltersMobileConnector'
import { SearchResultsMobile } from 'Algolia/components/SearchResult/index'
import WithJobOfferSearchListWrapper from './withJobOfferSearchListWrapper'
import JobsViewTopNavConnector from 'Candidate/connectors/JobsViewTopNav/JobsViewTopNavConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { isNotEmpty, isNotNil, notEquals } from 'Shared/helpers/Ramda'
import { isEmpty } from 'ramda'
import HitsLoader from 'Algolia/components/HitsLoader'
import { JobHitsLoader } from 'Shared/components/Main/MainLoaders'
import InfiniteScroll from 'Algolia/components/InfiniteScroll'
import { Trans } from 'react-i18next'
import { Link } from 'react-router-dom'
import Notification from 'Shared/components/Notification'
import OlivPlusBannerConnector from 'Candidate/connectors/OlivPlus/OlivPlusBannerConnector'
import UpdateSalaryExpectationNotificationConnector from 'Candidate/connectors/UpdateSalaryExpectationNotification/UpdateSalaryExpectationNotificationConnector'

const R = require('ramda')

class JobOfferSearchListMobile extends PureComponent {
  componentWillReceiveProps (nextProps) {
    R.when(
      R.allPass([
        R.propSatisfies(isNotNil, 'candidateId'),
        R.propSatisfies(isNotEmpty, 'searchStateValues'),
        R.propSatisfies(notEquals(this.props.searchStateValues), 'searchStateValues')
      ]),
      payload => {
        this.props.isCandidate && this.props.manualAmplitudeScripts.searchForJobs(payload.candidateId, R.prop('searchStateValues', payload))
      }
    )(nextProps)
  }

  profileCompletionNotification = () => {
    const { isCandidateProfileIncomplete, firstIncompleteStep } = this.props

    const notificationText = () => (
      <Trans i18nKey='notifications.candidate.searchJobsWithIncompleteProfile'>
        <Link to={`/candidate/profile/complete/${firstIncompleteStep}`} className='job-offers-view__notification-link'>Complete</Link> your profile to apply to jobs.
      </Trans>
    )

    return isCandidateProfileIncomplete && (
      <div className='job-offers-view-mobile__notification-container'>
        <Notification
          messageText={notificationText()}
          iconClassName='icon-im-alert-circle-outline'
          warning
        />
      </div>
    )
  }

  render () {
    const { isDefaultSearch, setSearchResults, searchResults, jobSearchData } = this.props
    return (
      <div>
        <JobsViewTopNavConnector withPreferencesLink />
        <JobsSearchFiltersMobileConnector jobSearchData={this.props.jobSearchData} searchResults={searchResults} resetJobSearchData={this.props.resetJobSearchData} sorting={this.props.sortMenu} />
        <UpdateSalaryExpectationNotificationConnector />
        {this.profileCompletionNotification()}
        <OlivPlusBannerConnector />
        <div className='jobs-view-mobile__list-container'>
          {!isEmpty(jobSearchData) && <div className='jobs-view-header__mobile'>{this.props.pageHeader}</div>}
          <SearchResultsMobile isDefaultSearch={isDefaultSearch} setSearchResults={setSearchResults} />
          <div>
            <HitsLoader loader={<JobHitsLoader />} />
            { searchResults !== 0 && <InfiniteScroll hitComponent={JobOfferCardAlgoliaMobileConnector} /> }
          </div>
        </div>
      </div>
    )
  }
}

export default compose(WithJobOfferSearchListWrapper, withTranslations)(JobOfferSearchListMobile)



