import APIInterceptor from './APIInterceptor'
import qs from 'query-string'

export default class CustomJobSearchService {
  constructor (api = APIInterceptor) {
    this.api = api
  }

  static options () {}

  fetchCustomJobsSearch (url, hash) {
    return this.api.get(`/CustomJobSearchPages/view/${url}?${qs.stringify({ hash })}`)
  }
}



