import React from 'react'
import { withRouter } from 'react-router-dom'
import ToggleRefinementList from 'Algolia/components/ToggleRefinementList'
import { getUrlToSearchState, getSearchStateValues } from 'Shared/selectors/url'
import { t } from 'i18next'
import * as R from 'ramda'

const EasyApplyRadioSelect = (props) => {
  const searchState = getUrlToSearchState(props.location.search, true)
  const searchStateValues = getSearchStateValues(searchState)
  const isSelected = R.pipe(
    R.propOr('', 'isExternalJobOffer'),
    R.equals([false])
  )(searchStateValues)

  return (
    <ToggleRefinementList
      attribute='isExternalJobOffer'
      name={t('searchAttributes.easyApply')}
      tooltip={t('searchAttributes.easyApplyTooltip')}
      isSelected={isSelected}
      refineValue={[false]}
    />
  )
}

export default withRouter(EasyApplyRadioSelect)



