import React from 'react'
import JobOfferSearchListWeb from './JobOfferSearchList'
import JobOfferSearchListMobile from './JobOfferSearchList.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const JobOfferSearchList = (props) => props.isMobile ? <JobOfferSearchListMobile {...props} /> : <JobOfferSearchListWeb {...props} />

export default withDeviceWidthDetector(JobOfferSearchList)



