import React, { Component } from 'react'
import { Roles } from 'Shared/constants/User'
import { StaticRoutes } from 'Shared/constants/Routes'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import CreateJobAlertFormConnector from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'
import Modal from 'Shared/components/Modal/index'
import { t } from 'i18next'
import './SaveJobAlertsButton.scss'

class SaveJobAlertsButton extends Component {
  state = {
    isJobAlertsFormOpen: false
  }

  openSaveJobsAlertsForm = () => this.setState({ isJobAlertsFormOpen: true })
  closeSaveJobsAlertsForm = () => this.setState({ isJobAlertsFormOpen: false })

  onCreateJobAlertClick = (e) => {
    if (this.props.userRole === Roles.guest.name) {
      this.props.history.push(StaticRoutes.candidate.jobAlerts + this.props.location.search)
    } else {
      this.openSaveJobsAlertsForm(e)
      e.currentTarget.blur()
    }
  }

  submitForm = () => {
    this.props.submitForm()
    this.props.canCloseModal && this.closeSaveJobsAlertsForm()
  }

  render () {
    return (
      <div className='save-job-alerts__btn-container'>
        {
          this.props.disableSaveJobAlertButton
            ? <Tooltip trigger='mouseenter' content={t('candidate.jobAlerts.disabledButtonTooltip')} followCursor>
              <div className='save-job-alerts__btn save-job-alerts__btn--disabled'>
                {t('common.createJobAlerts')}
              </div>
            </Tooltip>
            : <div className='save-job-alerts__btn save-job-alerts__btn--active' onClick={this.onCreateJobAlertClick}>
              {t('common.createJobAlerts')}
            </div>
        }
        <Modal
          title={t('modals.createJobAlert.title')}
          show={this.state.isJobAlertsFormOpen}
          handleSubmit={this.submitForm}
          handleClose={this.closeSaveJobsAlertsForm}
          handleCancel={this.closeSaveJobsAlertsForm}
          cancelText={t('common.cancel')}
          submitText={t('common.createJobAlert')}
          showContact={false}
          closeOnSubmit={false}
          showCancel
        >
          <p className='save-job-alerts__form-description'>{t('modals.createJobAlert.description')}</p>
          <CreateJobAlertFormConnector />
        </Modal>
      </div>
    )
  }
}

export default SaveJobAlertsButton



