import React, { Component } from 'react'
import { connectRefinementList } from 'react-instantsearch-dom'
import cx from 'classnames'
import './ToggleRefinementList.scss'
import Tooltip from 'Shared/components/Tooltip/Tooltip'

class ToggleRefinementList extends Component {
  state = {
    isSelected: this.props.isSelected
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.isSelected !== prevProps.isSelected) {
      this.setState({ isSelected: this.props.isSelected })
    }
  }

  toggleValue = () => {
    if (this.state.isSelected) {
      this.props.refine([])
    } else {
      const { refineValue } = this.props
      this.props.refine(refineValue)
    }
    this.setState({ isSelected: !this.state.isSelected })
  }

  render () {
    const { name, tooltip } = this.props
    const toggleClass = cx({
      'toggle-refinement': true,
      'parsing--hidden': true,
      'toggle-refinement--selected': this.state.isSelected,
      'toggle-refinement--loaded': this.props.isLoaded
    })

    return (
      <div className={toggleClass} onClick={this.toggleValue}>
        {name} {tooltip && <Tooltip content={tooltip}>
          <i className='ion-md-information-circle' />
        </Tooltip>}
      </div>
    )
  }
}

export default connectRefinementList(ToggleRefinementList)



