import React, { Component } from 'react'
import HideElement from 'Shared/hoc/HideElementOnScroll'
import SearchInputMobile from 'Algolia/components/SearchInput/SearchInput.mobile'
import CreateJobAlertFormConnector from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'
import * as R from 'ramda'
import { Roles } from 'Shared/constants/User'
import { StaticRoutes } from 'Shared/constants/Routes'
import { FormModalMobile } from 'Shared/components/Modal'
import MobileWindow from 'Shared/components/MobileWindow/MobileWindow.mobile'
import { t } from 'i18next'
import './JobsSearchFilters.scss'
import cx from 'classnames'
import { PrimaryButton, PrimaryButtonOutline } from 'Shared/components/Button'
import MultiSelectRefinementList from 'Algolia/components/MultiSelectRefinementList/MultiSelectRefinementList.mobile'
import JobOfferDatePostedRefinementList from 'Algolia/components/JobOfferDatePostedRefinementList'
import { sortJobDurationFilterItems } from 'Shared/helpers/filters'
import { transformItems } from 'Shared/helpers/Algolia'
import EasyApplyRadioSelect from 'Algolia/components/EasyApplyRadioSelect'

class JobsSearchFiltersMobile extends Component {
  state = {
    areCategoriesOpen: false,
    isCreateJobAlertFormOpen: false
  }

  openCreateJobAlertForm = () => this.setState({ isCreateJobAlertFormOpen: true })
  closeCreateJobAlertForm = () => this.setState({ isCreateJobAlertFormOpen: false })

  openCategoriesList = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'hidden'
    this.setState({ areCategoriesOpen: true })
  }

  closeCategoriesList = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'auto'
    this.setState({ areCategoriesOpen: false })
  }

  createJobAlertsButtonAction = () => {
    this.props.userRole === Roles.candidate.name
      ? this.openCreateJobAlertForm()
      : this.props.history.push(StaticRoutes.candidate.jobAlerts)
  }

  selectedFiltersCount = () => {
    const refinementListCount = R.pipe(
      R.propOr({}, 'refinementList'),
      R.values,
      R.map(R.length),
      R.sum
    )(this.props.searchState)

    const publishedAtCount = R.pipe(
      R.propOr({}, 'publishedAt'),
      R.ifElse(
        R.pipe(
          R.propOr('0', 'min'),
          R.equals('0')
        ),
        R.always(0),
        R.always(1)
      )
    )(this.props.searchState.range)

    return R.sum([refinementListCount, publishedAtCount])
  }

  resetFilters = () => {
    this.props.resetJobSearchData()
    this.props.history.push(this.props.history.location.pathname)
  }

  render () {
    const { submitForm, isFormValid, sorting, searchResults } = this.props
    const categoriesClass = cx({
      'jobs-search-filters-mobile__categories': true,
      'jobs-search-filters-mobile__categories--visible': this.state.areCategoriesOpen,
      'jobs-search-filters-mobile__categories--hidden': !this.state.areCategoriesOpen
    })
    const applyButtonText = searchResults
      ? t('common.applyFilterWithJobsNumber', { count: searchResults })
      : t('common.apply')

    return (
      <HideElement className='jobs-search-filters-mobile__search-container' resetLimit={57} showClass='jobs-search-filters-mobile__search-container--scrolled-up' elementHeight={57} hideClass='jobs-search-filters-mobile__search-container--scrolled'>
        <div className='jobs-search-filters-mobile__search-input-container'>
          <SearchInputMobile withDebounce />
        </div>
        <div className='jobs-search-filters-mobile__filters-more-container'>
          <div className='jobs-search-filters-mobile__filters-more'>
            <div className='jobs-search-filters-mobile__filters-more-btn'>
              <i onClick={this.openCategoriesList} className='jobs-search-filters-mobile__trigger-btn icon-im-filters'>
                {this.selectedFiltersCount() > 0 && <div className='jobs-search-filters-mobile__trigger-counter'>{this.selectedFiltersCount()}</div>}
              </i>
              {sorting}
            </div>
          </div>
        </div>
        <MobileWindow saveInDom show={this.state.areCategoriesOpen}>
          <div className={categoriesClass}>
            <div className='jobs-search-filters-mobile__categories-top-nav'>
              <div className='jobs-search-filters-mobile__categories-title'>{t('common.filters')}</div>
              <div className='jobs-search-filters-mobile__categories-top-nav-btns'>
                <div onClick={this.closeCategoriesList} className='icon-im-arrow-forward jobs-search-filters-mobile__categories-back' />
                <div onClick={this.resetFilters} className='jobs-search-filters-mobile__categories-reset'>{t('common.reset')}</div>
              </div>
            </div>
            <div className='jobs-search-filters-mobile__categories-content'>
              <JobOfferDatePostedRefinementList />
              <MultiSelectRefinementList
                attribute='city'
                name={t('searchAttributes.city')}
                labelWithCapitalizeFirst={false}
              />
              <MultiSelectRefinementList
                attribute='jobType'
                name={t('searchAttributes.jobType')}
              />
              <MultiSelectRefinementList
                attribute='department'
                name={t('searchAttributes.category')}
              />
              <MultiSelectRefinementList
                attribute='industry'
                name={t('searchAttributes.companyType')}
              />
              <EasyApplyRadioSelect />
              <MultiSelectRefinementList
                attribute='commitment'
                name={t('searchAttributes.commitment')}
              />
              <MultiSelectRefinementList
                attribute='duration'
                name={t('searchAttributes.duration')}
                handleCustomSort={sortJobDurationFilterItems}
              />
              <MultiSelectRefinementList
                attribute='country'
                name={t('searchAttributes.country')}
                labelWithCapitalizeFirst={false}
                withAlgoliaSearch={false}
                transformItems={transformItems.country(this.props.countries)}
              />
            </div>
            <div className='jobs-search-filters-mobile__categories-bottom-nav'>
              <PrimaryButtonOutline onClick={this.createJobAlertsButtonAction}>{t('common.createJobAlert')}</PrimaryButtonOutline>
              <PrimaryButton onClick={this.closeCategoriesList}>{applyButtonText}</PrimaryButton>
            </div>
          </div>
        </MobileWindow>
        <FormModalMobile
          title={t('common.createJobAlerts')}
          show={this.state.isCreateJobAlertFormOpen}
          handleClose={this.closeCreateJobAlertForm}
          handleSubmit={submitForm}
          submitText={t('common.createJobAlert')}
          cancelText={t('common.cancel')}
          disableSubmit={!isFormValid}
        >
          <p className='job-alerts-mobile__form-description'>{t('modals.createJobAlert.description')}</p>
          <CreateJobAlertFormConnector />
        </FormModalMobile>
      </HideElement>
    )
  }
}

export default JobsSearchFiltersMobile



