import React, { Component } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import throttle from 'lodash/throttle'

class HideElement extends Component {
  state = {
    delta: 50,
    elementHeight: this.props.elementHeight,
    didScroll: true,
    lastScrollTop: 0,
    elementClass: ''
  }

  componentDidMount () {
    window.addEventListener('scroll', this.handleScroll)
  }

  componentWillUnmount () {
    window.removeEventListener('scroll', this.handleScroll)
  }

  hideElement = () => this.setState({ elementClass: this.props.hideClass })

  showElement = () => this.setState({ elementClass: this.props.showClass })

  resetClass = () => this.setState({ elementClass: '' })

  getDocHeight = () => {
    return Math.max(
      document.body.scrollHeight, document.documentElement.scrollHeight,
      document.body.offsetHeight, document.documentElement.offsetHeight,
      document.body.clientHeight, document.documentElement.clientHeight
    )
  }

  hasScrolled = () => {
    const scrollTop = window.scrollY
    const scrolledLessThanDelta = Math.abs(this.state.lastScrollTop - scrollTop) <= this.state.delta
    const scrolledMoreThanElementHeight = scrollTop > this.state.lastScrollTop && scrollTop > this.state.elementHeight

    if (scrolledLessThanDelta) return
    if (scrolledMoreThanElementHeight) {
      this.hideElement()
    } else {
      if (scrollTop <= this.props.resetLimit) {
        this.resetClass()
      } else if (scrollTop < this.getDocHeight()) {
        this.showElement()
      }
    }

    this.setState({ lastScrollTop: scrollTop })
  }

  throttledScroll = throttle(this.hasScrolled, 250)

  handleScroll = () => {
    this.setState({ didScroll: true })
    this.throttledScroll()
  }

  render () {
    const classNames = cx({
      [this.props.className]: true,
      [this.state.elementClass]: true,
      [this.props.shadowClass]: this.props.shadowClass && window.pageYOffset > this.props.elementHeight
    })
    return (
      <div className={classNames}>
        {this.props.children}
      </div>
    )
  }
}

HideElement.propTypes = {
  className: PropTypes.string,
  hideClass: PropTypes.string.isRequired,
  showClass: PropTypes.string,
  elementHeight: PropTypes.number.isRequired,
  children: PropTypes.node,
  shadowClass: PropTypes.string
}

HideElement.defaultProps = {
  showClass: '',
  resetLimit: 0
}

export default HideElement



