import { connect } from 'react-redux'
import UpdateSalaryExpectationNotification from 'Candidate/components/UpdateSalaryExpectationNotification/UpdateSalaryExpectationNotification'
import { submit, hasSubmitSucceeded } from 'redux-form'
import { formName } from 'Candidate/connectors/Forms/SalaryExpectationFormFields/SalaryExpectationFormConnector'
import { isSalaryRangeMissing } from 'Shared/selectors/user'
import { Roles } from 'Shared/constants/User'

const mapStateToProps = state => ({
  isCandidate: state.user.role === Roles.candidate.name,
  isSalaryExpectationMissing: isSalaryRangeMissing(state),
  isSalaryExpectationFormSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitSalaryExpectationForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(UpdateSalaryExpectationNotification)



