import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connectRange } from 'react-instantsearch-dom'
import * as R from 'ramda'
import { t } from 'i18next'
import cx from 'classnames'
import './RangeSelectRefinementList.scss'

class RangeSelectRefinementList extends Component {
  state = {
    value: '',
    isOpen: false
  }

  componentDidMount () {
    this.setState({ value: this.props.getInitialValue(this.props.currentRefinement) })
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.location.search !== this.props.location.search) {
      this.setState({ value: this.props.getInitialValue(this.props.currentRefinement) })
    }
  }

  currentRefinementLength = () => {
    const { defaultValue } = this.props
    const getCounter = () => <div className='range-select-refinement-list__count'>{1}</div>

    return R.ifElse(
      R.equals(defaultValue),
      R.always(''),
      getCounter
    )(this.state.value)
  }

  onSelectRadio = value => () => {
    this.setState({ value })
    return this.props.refine(this.props.refineValues(value))
  }

  refinementListItem = (item) => (
    <div key={item.inputValue} >
      <label className='range-select-refinement-list-mobile__radio-label'>
        <span>{t(item.translationPath)} </span>
        <input
          className='range-select-refinement-list-mobile__radio'
          type='radio'
          onChange={this.onSelectRadio(item.inputValue)}
          value={item.inputValue}
          checked={this.state.value === item.inputValue}
        />
        <span className='range-select-refinement-list-mobile__checkmark' />
      </label>
    </div>
  )

  refinementListItems = () => {
    return R.map(this.refinementListItem)(this.props.options)
  }

  openFilter = () => this.setState({ isOpen: true })
  closeFilter = () => this.setState({ isOpen: false })

  render () {
    const { name } = this.props
    const itemsClassName = cx({
      'range-select-refinement-list-mobile__list': true,
      'range-select-refinement-list-mobile__list--visible': this.state.isOpen,
      'range-select-refinement-list-mobile__list--hidden': !this.state.isOpen
    })

    return (
      <div className='range-select-refinement-list-mobile__container parsing--hidden'>
        <div className='range-select-refinement-list-mobile__category' onClick={this.openFilter}>
          <div><span>{name}</span>{this.currentRefinementLength()}</div>
          <i className='icon-im-caret-right' />
        </div>
        <div className={itemsClassName}>
          <div className='range-select-refinement-list-mobile__top-nav'>
            <div className='range-select-refinement-list-mobile__title'>{name}</div>
            <div className='range-select-refinement-list-mobile__top-nav-btns'>
              <div onClick={this.closeFilter} className='icon-im-arrow-forward range-select-refinement-list-mobile__back' />
              <div onClick={this.closeFilter} className='range-select-refinement-list-mobile__done'>{t('common.done')}</div>
            </div>
          </div>
          <div className='range-select-refinement-list-mobile__items'>
            <fieldset>
              {this.refinementListItems()}
            </fieldset>
          </div>
        </div>
      </div>
    )
  }
}

export default connectRange(withRouter(RangeSelectRefinementList))



