import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import SearchInput from 'Algolia/components/SearchInput'
import MultiSelectRefinementList from 'Algolia/components/MultiSelectRefinementList'
import Container from 'Shared/components/Container'
import { containerSizes } from 'Shared/constants/Styles'
import './JobsSearchFilters.scss'
import { t } from 'i18next'
import * as R from 'ramda'
import cx from 'classnames'
import { isArabicLayout } from 'Shared/helpers/layout'
import SaveJobAlertsButtonConnector from 'Candidate/connectors/JobsSearchFilters/SaveJobAlertsButtonConnector'
import JobOfferDatePostedRefinementList from 'Algolia/components/JobOfferDatePostedRefinementList'
import JobsMoreSearchFilters from './components/JobsMoreSearchFilters'

class JobsSearchFilters extends Component {
  state = {
    showMoreFilters: false,
    currentSearch: ''
  }

  componentDidMount () {
    document.getElementsByTagName('body')[0].style.overflow = 'auto'
  }

  selectedFiltersCount = () => {
    const refinementListCount = R.pipe(
      R.propOr({}, 'refinementList'),
      R.values,
      R.map(R.length),
      R.sum
    )(this.props.searchState)

    const publishedAtCount = R.pipe(
      R.propOr({}, 'publishedAt'),
      R.ifElse(
        R.pipe(
          R.propOr('0', 'min'),
          R.equals('0')
        ),
        R.always(0),
        R.always(1)
      )
    )(this.props.searchState.range)

    return R.sum([refinementListCount, publishedAtCount])
  }

  handleReset = () => {
    this.props.resetJobSearchData()
    this.props.history.push(this.props.history.location.pathname)
  }

  clearButton = () => {
    const btnClass = cx({
      'jobs-search-filters__filters-clear-btn': true,
      'jobs-search-filters__filters-clear-btn--arabic': isArabicLayout
    })

    return this.selectedFiltersCount() > 0 && (
      <div onClick={this.handleReset} className={btnClass}>
        {t('common.clearFilters')}
        {this.selectedFiltersCount() > 0 && <div className='jobs-search-filters__filters-clear-counter'>{this.selectedFiltersCount()}</div>}
      </div>
    )
  }

  showMoreFilters = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'hidden'
    const currentSearch = this.props.location.search
    return this.setState({ showMoreFilters: true, currentSearch })
  }
  hideMoreFilters = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'auto'
    return this.setState({ showMoreFilters: false })
  }

  render () {
    return (
      <Container size={containerSizes.large} className='jobs-search-filters parsing--hidden'>
        <div className='jobs-search-filters__search-container'>
          <SearchInput withDebounce additionalButton={<SaveJobAlertsButtonConnector />} placeholder={t('searchPlaceholders.jobOfferSearch')} />
        </div>
        <div className='columns'>
          <div className='column col-9 col-xl-8'>
            <div className='jobs-search-filters__filters-container'>
              <JobOfferDatePostedRefinementList />
              <MultiSelectRefinementList
                attribute='city'
                name={t('searchAttributes.city')}
                labelWithCapitalizeFirst={false}
                withPopup
              />
              <MultiSelectRefinementList
                attribute='jobType'
                name={t('searchAttributes.jobType')}
                withPopup
              />
              <MultiSelectRefinementList
                attribute='department'
                name={t('searchAttributes.category')}
                withPopup
              />
              <MultiSelectRefinementList
                attribute='industry'
                name={t('searchAttributes.companyType')}
                withPopup
              />
            </div>
          </div>
          <div className='column col-3 col-xl-4'>
            <div className='jobs-search-filters__filters-more'>
              {this.clearButton()}
              <div className='jobs-search-filters__filters-more-btn' onClick={this.showMoreFilters}>
                {t('common.moreFilters')}
              </div>
            </div>
          </div>
        </div>
        <JobsMoreSearchFilters
          currentSearch={this.state.currentSearch}
          hideMoreFilters={this.hideMoreFilters}
          additionalButtons={this.clearButton()}
          showMoreFilters={this.state.showMoreFilters}
          countries={this.props.countries}
        />
      </Container>
    )
  }
}

export default withRouter(JobsSearchFilters)



