import React from 'react'
import {
  DATE_POSTED_RADIO_FILTER_ITEMS,
  DATE_POSTED_INPUT_VALUES
} from 'Shared/constants/Algolia'
import RangeSelectRefinementListWeb from 'Algolia/components/RangeSelectRefinementList'
import RangeSelectRefinementListMobile from 'Algolia/components/RangeSelectRefinementList/RangeSelectRefinementList.mobile'
import { currentTimestamp, oneDayTimestamp } from 'Shared/constants/Date'
import { t } from 'i18next'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const JobOfferDatePostedRefinementList = (props) => {
  const getMin = (value) => {
    switch (value) {
      case DATE_POSTED_INPUT_VALUES.day.value:
        return currentTimestamp - oneDayTimestamp
      case DATE_POSTED_INPUT_VALUES.threeDays.value:
        return currentTimestamp - oneDayTimestamp * 3
      case DATE_POSTED_INPUT_VALUES.week.value:
        return currentTimestamp - oneDayTimestamp * 7
      case DATE_POSTED_INPUT_VALUES.month.value:
        return currentTimestamp - oneDayTimestamp * 30
      case DATE_POSTED_INPUT_VALUES.all.value:
      default:
        return 0
    }
  }

  const getSelectecdValue = timestamp => {
    const isTimestampWithinDays = days =>
      timestamp !== 0 &&
      timestamp >= (currentTimestamp - oneDayTimestamp * days) - oneDayTimestamp &&
      timestamp <= (currentTimestamp - oneDayTimestamp * days) + oneDayTimestamp

    switch (true) {
      case isTimestampWithinDays(1):
        return DATE_POSTED_INPUT_VALUES.day
      case isTimestampWithinDays(3):
        return DATE_POSTED_INPUT_VALUES.threeDays
      case isTimestampWithinDays(7):
        return DATE_POSTED_INPUT_VALUES.week
      case isTimestampWithinDays(30):
        return DATE_POSTED_INPUT_VALUES.month
      default:
        return DATE_POSTED_INPUT_VALUES.all
    }
  }

  const refineValues = value => ({ min: getMin(value), max: currentTimestamp })
  const getInitialValue = currentRefinement => getSelectecdValue(currentRefinement.min).value

  const RangeSelectRefinementList = props.isMobile ? RangeSelectRefinementListMobile : RangeSelectRefinementListWeb

  return (
    <RangeSelectRefinementList
      defaultValue='all'
      options={DATE_POSTED_RADIO_FILTER_ITEMS}
      refineValues={refineValues}
      getInitialValue={getInitialValue}
      attribute='publishedAt'
      name={t('searchAttributes.datePosted.title')}
    />
  )
}

export default withDeviceWidthDetector(JobOfferDatePostedRefinementList)



