import React from 'react'
import JobsEmptyStateListWeb from './JobsEmptyStateList'
import JobsEmptyStateListMobile from './JobsEmptyStateList.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const JobsEmptyStateList = (props) => props.isMobile ? <JobsEmptyStateListMobile {...props} /> : <JobsEmptyStateListWeb {...props} />

export default withDeviceWidthDetector(JobsEmptyStateList)



