import { durationFilterItemsOrder } from 'Shared/constants/Filters'
import { findByEqualLabel } from 'Shared/helpers/collection'
import * as R from 'ramda'

export const sortingMethod = key => (a, b) => {
  const aMappedFilter = findByEqualLabel(durationFilterItemsOrder, a.label)
  const bMappedFilter = findByEqualLabel(durationFilterItemsOrder, b.label)
  return aMappedFilter[key] - bMappedFilter[key]
}

export const sortJobDurationFilterItems = R.sort(sortingMethod('order'))



