import { connectInfiniteHits } from 'react-instantsearch-dom'
import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import TrackVisibility from 'react-on-screen'
import Loader from 'Shared/components/Loader/Loader'
import { uniqBy, prop } from 'ramda'

function AnchorHit (props) {
  const { hasMore, refine } = props

  useEffect(() => {
    if (hasMore) {
      refine()
    }
  }, [])

  useEffect(() => {
    if (hasMore) {
      refine()
    }
  }, [hasMore])

  return (
    <div style={{ height: '10px', width: '100%' }}>&nbsp;</div>
  )
}

function InfiniteHits (props) {
  const hits = () => {
    const { hitComponent, viewedFilterValue } = props
    const HitComponent = hitComponent
    const filteredHits = uniqBy(prop('id'))(props.hits)

    return filteredHits
      .map((hit, index) => <HitComponent
        key={hit.objectID}
        hit={hit}
        hitNumber={index}
        viewedFilterValue={viewedFilterValue}
      />)
  }

  return (
    <div className={props.infiniteScrollClass}>
      {hits()}
      <TrackVisibility offset={10} className='column col-12'>
        {({ isVisible }) => isVisible && <AnchorHit refine={props.refine} hasMore={props.hasMore} />}
      </TrackVisibility>
      { props.hasMore && <div style={{ margin: '20px 0', width: '100%' }}><Loader /></div> }
    </div>
  )
}

InfiniteHits.defaultProps = {
  hits: []
}

InfiniteHits.propTypes = {
  hits: PropTypes.array.isRequired
}

export default connectInfiniteHits(InfiniteHits)



