import React from 'react'
import './HitLoader.scss'

const JobOfferHitLoader = () => (
  <div className='column col-4 col-lg-6 col-md-12'>
    <div className='hit-loader'>
      <div className='hit-loader__checkbox-container'>
        <div className='hit-loader__checkbox' />
      </div>
      <div className='hit-loader__avatar-container'>
        <div className='hit-loader__avatar' />
      </div>
      <div className='hit-loader__buttons'>
        <div className='hit-loader__btn-skill' />
        <div className='hit-loader__btn-skill' />
        <div className='hit-loader__btn-skill' />
      </div>
    </div>
  </div>
)

export default JobOfferHitLoader



