import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { submit, isValid } from 'redux-form'
import JobsSearchFiltersMobile from 'Candidate/components/JobsSearchFilters/JobsSearchFilters.mobile'
import { formName } from 'Candidate/connectors/Forms/JobAlerts/CreateJobAlertFormConnector'
import { getUrlToSearchState } from 'Shared/selectors/url'
import { getUserRole } from 'Shared/selectors/user'

const mapStateToProps = (state, ownProps) => {
  const searchState = { ...getUrlToSearchState(ownProps.location.search, true), ...ownProps.jobSearchData }

  return ({
    isFormValid: isValid(formName)(state),
    userRole: getUserRole(state),
    searchState,
    countries: state.resources.countries.entries
  })
}

const mapDispatchToProps = dispatch => ({
  submitForm: () => dispatch(submit(formName))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(JobsSearchFiltersMobile))



