import React, { PureComponent } from 'react'
import { withRouter } from 'react-router-dom'
import { Pagination } from 'react-instantsearch-dom'
import withTranslations from 'Shared/hoc/withTranslations/index'
import JobOfferCardAlgoliaConnector from 'Candidate/connectors/JobOfferCardAlgolia/JobOfferCardAlgoliaConnector'
import Container from 'Shared/components/Container/index'
import NumberedHits from 'Algolia/components/NumberedHits'
import WithJobOfferEmptyStateWrapper from './withJobOfferEmptyStateWrapper'
import 'Shared/assets/scss/components/_algolia-pagination.scss'

class JobsEmptyStateList extends PureComponent {
  render () {
    return (
      <Container className='job-offers-view'>
        <div className='job-offers-view--empty'>
          <div className='job-offers-view--empty-icon'>
            <i className='icon-im-dissatisfied' />
          </div>
          <div>{this.props.t('jobOffersList.emptyState')}</div>
        </div>
        <div className='jobs-offers-view--empty-header'>
          {this.props.t('jobOffersList.checkAvailableJobs')}
        </div>
        <div className='job-offers-view__container'>
          <NumberedHits hitComponent={JobOfferCardAlgoliaConnector} />
        </div>
        <div className='c-algolia-pagination'>
          <Pagination />
        </div>
      </Container>
    )
  }
}

export default withRouter(WithJobOfferEmptyStateWrapper(withTranslations(JobsEmptyStateList)))



