import { ISOcodes } from 'Shared/constants/i18n'
import LanguageService from 'Shared/services/LanguageService'
import { searchStateMergedValues } from 'Shared/helpers/Algolia'

export const isJobsDefaultSearch = (searchState, filters) => searchStateMergedValues(searchState, filters)[0] === `languageIsoCode:${ISOcodes[LanguageService.defaultLanguage]}`



