import React from 'react'
import { withRouter } from 'react-router-dom'
import RadioSelectRefinementList from 'Algolia/components/RadioSelectRefinementList'
import { t } from 'i18next'
import * as R from 'ramda'

const handleRefine = (value, refine) => {
  switch (value) {
    case 'no':
      return refine(['true'])
    case 'yes':
      return refine(['false'])
    default:
      return refine([])
  }
}

const findCount = items => label => R.pipe(
  R.find(
    R.pipe(
      R.propOr('', 'label'),
      R.equals(label)
    )
  ),
  R.propOr(0, 'count')
)(items)

const getAllCount = items => label => R.pipe(
  R.map(
    R.prop('count')
  ),
  R.sum
)(items)

const options = items => {
  const getCount = R.ifElse(
    R.equals('All'),
    getAllCount(items),
    findCount(items)
  )

  return [
    {label: `All (${getCount('All')})`, value: 'all'},
    {label: `No (${getCount('true')})`, value: 'no'},
    {label: `Yes (${getCount('false')})`, value: 'yes'}
  ]
}

const EasyApplyRadioSelect = (props) => {
  return (
    <RadioSelectRefinementList
      attribute='isExternalJobOffer'
      name={t('searchAttributes.easyApply')}
      getOptions={options}
      handleRefine={handleRefine}
      initialValue={'all'}
      withPopup
      tooltip={t('searchAttributes.easyApplyTooltip')}
      {...props}
    />
  )
}

export default withRouter(EasyApplyRadioSelect)



