import React, { Component, Fragment } from 'react'
import { Helmet } from 'react-helmet'
import { t } from 'i18next'
import cx from 'classnames'
import PropTypes from 'prop-types'
import URLSearchParams from 'url-search-params'
import { humanize, formatString } from 'Shared/helpers/string'
import DropdownMenu from 'Shared/components/DropdownMenu/index'
import DropdownArrow from 'Shared/components/DropdownArrow/index'
import INDEX_NAME from 'Shared/constants/Algolia'
import { Roles } from 'Shared/constants/User'
import { jobsPage } from 'Shared/constants/MetaTags'
import { isJobsDefaultSearch } from 'Shared/helpers/jobs'
import moment from 'moment'
import { isEmpty, propOr, pathOr, isNil, path } from 'ramda'
import { isMobile } from 'Shared/helpers/mobile'
import LanguageService from 'Shared/services/LanguageService'
import { searchStateToUrl, urlToSearchState } from 'Shared/helpers/querySearch'

const currentYear = moment().year()

const SortOptions = [INDEX_NAME.JOB_OFFERS, INDEX_NAME.JOB_OFFERS_RECENT]

const WithJobOfferSearchListWrapper = (ComposedComponent) => class HOC extends Component {
  state = {
    isDropdownOpen: false,
    selectedSortOption: this.props.indexName,
    isDescriptionOpen: false
  }

  isCandidate = this.props.role === Roles.candidate.name

  sortOptionActionsMap = {
    [INDEX_NAME.JOB_OFFERS]: () => {
      this.updateQuerySearch('sort', INDEX_NAME.JOB_OFFERS)
      this.props.addFilter({ majors: `majors:${propOr('', 'majorId', this.props.lastCandidateMajor)}` }, true)
      return this.props.changeIndex(INDEX_NAME.JOB_OFFERS)
    },
    [INDEX_NAME.JOB_OFFERS_RECENT]: () => {
      this.updateQuerySearch('sort', INDEX_NAME.JOB_OFFERS_RECENT)
      this.props.removeFilter(['majors'], true)
      return this.props.changeIndex(INDEX_NAME.JOB_OFFERS_RECENT)
    }
  }

  async componentDidMount () {
    this.props.fetchCountriesIfNeeded()
    this.props.fetchCurrentLocationIfNeeded().then(() => {
      const currentSearch = urlToSearchState(this.props.location.search)
      this.props.currentLocation.jobOfferCountries.length &&
      (isNil(currentSearch.refinementList) || isEmpty(currentSearch.refinementList) || !path(['refinementList', 'country'], currentSearch).length) &&
      this.props.history.replace(`${this.props.history.location.pathname}?${searchStateToUrl({
        ...currentSearch,
        refinementList: {
          country: this.props.currentLocation.jobOfferCountries
        }
      })}`)
      this.props.forceRefresh()
    }).catch((e) => console.error(e))
    if (this.props.role === Roles.candidate.name) {
      await this.props.fetchUserEducationDetails()
      this.props.addFilter({ majors: `majors:${propOr('', 'majorId', this.props.lastCandidateMajor)}` }, true)
      this.props.fetchJobOffersBookmarksForCandidate(this.props.candidateId)
      this.props.fetchCandidateApplicationsIfNeeded(this.props.candidateId)
    }
  }

  updateQuerySearch = (key, value) => {
    const { history } = this.props
    const query = new URLSearchParams(history.location.search)

    query.set(key, value)

    history.replace({...history.location, search: query.toString()})
  }

  toggleArrow = isDropdownOpen => this.setState({ isDropdownOpen })

  closeDropdown = () => this.setState({ isDropdownOpen: false })

  get triggerButton () {
    return (
      <div className='column col-sm-12'>
        <div className='job-offers-view__sort-trigger'>
          <span>{this.props.t('common.sortedBy')}</span>
          <span>{this.props.t(`jobOffersList.sortDropdown.${this.state.selectedSortOption}`)}</span>
          <DropdownArrow isOpen={this.state.isDropdownOpen} />
        </div>
      </div>
    )
  }

  setSelectedOption = selectedSortOption => () => {
    this.sortOptionActionsMap[selectedSortOption]()
    // refreshes the page for infinite hits sorting
    isMobile() && window.location.reload()
    return this.setState({ selectedSortOption: selectedSortOption })
  }

  get sortMenuItems () {
    return SortOptions.map((item, index) => {
      const itemClassName = cx({
        'job-offers-sort-item': true,
        'job-offers-sort-item--selected': this.state.selectedSortOption === item
      })

      return (
        <li className={itemClassName} key={`${index}-${item}`} onClick={this.setSelectedOption(item)}>
          {this.props.t(`jobOffersList.sortDropdown.${item}`)}
        </li>
      )
    })
  }

  componentWillUpdate (nextProps, nextState) {
    const englishDescription = pathOr('', ['pageDetails', LanguageService.EN, 'pageDescription'], nextProps.jobSearchData)
    const newDescription = pathOr(englishDescription, ['pageDetails', LanguageService.userLanguage, 'pageDescription'], nextProps.jobSearchData)
    if (this.getPageDetail('pageDescription') !== newDescription && newDescription.length < 1300) {
      this.setState({ isDescriptionOpen: true })
    }
  }

  getPageDetail = detail => {
    const englishDescription = pathOr('', ['pageDetails', LanguageService.EN, detail], this.props.jobSearchData)
    return pathOr(englishDescription, ['pageDetails', LanguageService.userLanguage, detail], this.props.jobSearchData)
  }

  get pageTitle () {
    const { match: { params: { jobType, city, department } }, jobSearchData } = this.props
    switch (true) {
      case !isEmpty(jobSearchData):
        return this.getPageDetail('pageTitle')
      case !!(department && jobType && city):
        return `${currentYear} ${humanize(department)} ${humanize(jobType)}s in ${humanize(city)} — Oliv`
      case (jobType && city) && !department:
        return `${currentYear} ${humanize(jobType)}s in ${humanize(city)} — Oliv`
      case (jobType && department) && !city:
        return `${currentYear} ${humanize(department)} ${humanize(jobType)}s — Oliv`
      default:
        return jobsPage.title(this.props.match)
    }
  }

  toggleDescription = () => this.setState({ isDescriptionOpen: !this.state.isDescriptionOpen })

  get pageHeader () {
    const { jobSearchData } = this.props
    switch (true) {
      case !isEmpty(jobSearchData):
        const descriptionClassName = cx({
          'job-offers-view__description': true,
          'job-offers-view__description--closed': !this.state.isDescriptionOpen,
          'job-offers-view__description--opened': this.state.isDescriptionOpen
        })
        const description = this.getPageDetail('pageDescription')
        return <Fragment>
          <h1 className='job-offers-view__header'>{this.getPageDetail('heading')}</h1>
          <p className={descriptionClassName}>{description}</p>
          {description.length > 1300 &&
            <a className='job-offers-view__read-more' onClick={this.toggleDescription}>
              {this.state.isDescriptionOpen ? t('common.readLess') : t('common.readMore')}
            </a>
          }
        </Fragment>
      default:
        return null
    }
  }

  get pageDescription () {
    const { searchResults, match: { params: { jobType, city, department } }, searchQueryFromUrl, jobSearchData } = this.props
    const jobsNumber = searchResults > 0 ? `${searchResults} ` : ''
    const changePluralJobs = searchResults === 1 ? humanize(jobType) : `${humanize(jobType)}s`

    switch (true) {
      case !isEmpty(jobSearchData):
        return this.getPageDetail('metaDescription')
      case !!(department && jobType && city):
        return `${jobsNumber}top ${humanize(department)} ${changePluralJobs} in ${humanize(city)}. Oliv helps university students and recent graduates like you find part-time jobs, internships and graduate jobs in ${humanize(city)}.`
      case (jobType && city) && !department:
        return `${jobsNumber}top ${changePluralJobs} in ${humanize(city)}. Oliv helps university students and recent graduates like you find part-time jobs, internships and graduate jobs in ${humanize(city)}.`
      case (jobType && department) && !city:
        return `${jobsNumber}top ${humanize(department)} ${changePluralJobs}. Oliv helps university students and recent graduates like you find part-time jobs, internships and graduate jobs.`
      default:
        return jobsPage.description(jobsNumber, searchQueryFromUrl)
    }
  }

  get sortMenu () {
    switch (true) {
      case this.isCandidate && isMobile():
        return (
          <DropdownMenu
            onClose={this.closeDropdown}
            triggerButton={<i className='ion-md-funnel jobs-view-mobile__sorting-btn' />}
            align={'right'}
            closeOnOutsideClick
          >
            {this.sortMenuItems}
          </DropdownMenu>
        )
      case this.isCandidate && !isMobile():
        return (
          <DropdownMenu
            onToggle={this.toggleArrow}
            onClose={this.closeDropdown}
            triggerButton={this.triggerButton}
            align={'right'}
            closeOnOutsideClick
          >
            {this.sortMenuItems}
          </DropdownMenu>
        )
      default:
        return null
    }
  }

  render () {
    const { match, searchResults, jobSearchData } = this.props
    return (
      <div className='job-offers-view'>
        <Helmet>
          <title>{this.pageTitle}</title>
          <meta name='description' content={formatString(this.pageDescription, 'upperfirst')} />
          <meta property='og:title' content={jobsPage.title(match)} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={jobsPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={jobsPage.url} />
          <meta property='og:description' content={formatString(this.pageDescription, 'upperfirst')} />
        </Helmet>
        <ComposedComponent
          {...this.props}
          pageHeader={this.pageHeader}
          isCandidate={this.isCandidate}
          setSearchResults={this.props.setSearchResults}
          isDefaultSearch={isJobsDefaultSearch(this.props.searchState, this.props.filters)}
          sortMenu={this.sortMenu}
          searchResults={searchResults}
          jobSearchData={jobSearchData}
        />
      </div>
    )
  }
}

WithJobOfferSearchListWrapper.defaultProps = {
  lastCandidateMajor: {}
}

WithJobOfferSearchListWrapper.propTypes = {
  lastCandidateMajor: PropTypes.object.isRequired,
  fetchJobOffersBookmarksForCandidate: PropTypes.func.isRequired,
  fetchCandidateApplicationsIfNeeded: PropTypes.func.isRequired,
  candidateId: PropTypes.string
}

export default WithJobOfferSearchListWrapper



