import React, { PureComponent } from 'react'
import { equals, omit, ifElse, isNil, assoc, when, has, pipe, dissoc, not, pathOr, split, lensPath, set, head } from 'ramda'
import PropTypes from 'prop-types'
import { InstantSearch, Configure } from 'react-instantsearch-dom'
import { searchStateToUrl } from 'Shared/helpers/querySearch'

// TODO When withAlgolia is wrapped with component connectors it's refreshed many many times. Consider to chage it to
// TODO service, instead component, with some options instead HOC
const withAlgolia = (ComposedComponent) => class HOC extends PureComponent {
  state = {
    filters: this.props.filters,
    optionalFilters: this.props.optionalFilters,
    indexName: this.props.indexName,
    refresh: false,
    objectIdFilters: ''
  }

  static defaultProps = {
    filters: {},
    optionalFilters: {},
    configureProps: {},
    shouldUpdateUrl: true,
    onChangeSearchState: () => {}
  }

  static propTypes = {
    filters: PropTypes.object,
    indexName: PropTypes.string.isRequired,
    configureProps: PropTypes.object,
    searchState: PropTypes.object
  }

  componentDidUpdate (prevProps, prevState) {
    if (this.state.objectIdFilters !== prevState.objectIdFilters) {
      this.setState({ page: 1 })
    }

    if (not(equals(this.state.filters, prevState.filters))) {
      this.setState({ page: 1 })
    }

    if (prevState.refresh && prevState.refresh !== this.state.refresh) {
      this.setState({ page: 1 })
    }
  }

  addFilter = (filter, isOptional) => {
    return ifElse(
      isNil,
      () => this.setState({ filters: { ...this.state.filters, ...filter } }),
      () => this.setState({ optionalFilters: { ...this.state.optionalFilters, ...filter } })
    )(isOptional)
  }

  removeFilter = (filterKey, isOptional) => {
    return ifElse(
      isNil,
      () => this.setState({ filters: omit(filterKey, this.state.filters) }),
      () => this.setState({ optionalFilters: omit(filterKey, this.state.optionalFilters) })
    )(isOptional)
  }

  updateFilter = (filterKey, value, isOptional) => {
    return ifElse(
      isNil,
      () => this.setState({ filters: { ...this.state.filters, [filterKey]: value } }),
      () => this.setState({ optionalFilters: { ...this.state.optionalFilters, [filterKey]: value } })
    )(isOptional)
  }

  parseFiltersToQuery = (isOptional) => {
    const { filters, optionalFilters } = this.state
    const parseObjectToQuery = object => Object.keys(object).reduce((prev, next, index, array) => {
      return array.length - 1 > index
        ? prev + object[next] + ' AND '
        : prev + object[next]
    }, '')

    return ifElse(
      isNil,
      () => parseObjectToQuery(filters),
      () => parseObjectToQuery(optionalFilters)
    )(isOptional)
  }

  changeIndex = indexName => this.setState({ indexName })

  onChangeSearchState = (searchState) => {
    const pureFilters = pipe(
      pathOr('', ['configure', 'filters']),
      split(/AND objectID:/),
      head
    )(searchState)
    const lensConfigureFilters = lensPath(['configure', 'filters'])
    const removeObjectIdFilters = set(lensConfigureFilters, pureFilters)
    const searchStateUrl = pipe(
      dissoc('page'),
      removeObjectIdFilters
    )(searchState)

    const updateUrlWithSearchState = () => {
      return this.setState({ refresh: false }, () =>
        this.props.history.push(`${this.props.location.pathname}?${searchStateToUrl(searchStateUrl)}`))
    }

    this.props.onChangeSearchState({ ...searchState, ...this.state.filters, ...this.state.optionalFilters })
    // this workaround helps to keep information of page number in withAlgolia hoc, but we have to keep page number away from url
    // due to weird behaviour on infinite scroll (mobile view)

    this.setState({ page: searchState.page })

    return when(
      equals(true),
      updateUrlWithSearchState
    )(this.props.shouldUpdateUrl)
  }

  forceRefresh = () => {
    return this.setState({ refresh: true }, () => this.setState({ refresh: false }))
  }

  addObjectIdFilters = filterMethod => this.setState({ objectIdFilters: filterMethod })

  render () {
    const optionalFilters = this.parseFiltersToQuery(true)
    const configureProps = this.props.configureProps

    if (optionalFilters) {
      configureProps.optionalFilters = optionalFilters
    }

    const searchState = this.props.searchState && has('page', this.state) && typeof this.state.page === 'number' ? assoc('page', this.state.page, this.props.searchState) : this.props.searchState

    return (
      <InstantSearch
        appId={process.env.REACT_APP_ALGOLIA_APP_ID}
        apiKey={process.env.REACT_APP_ALGOLIA_API_KEY}
        indexName={`${process.env.REACT_APP_ALGOLIA_PREFIX}_${this.state.indexName}`}
        onSearchStateChange={this.onChangeSearchState}
        searchState={searchState}
        refresh={this.state.refresh}
      >
        <Configure {...configureProps} filters={this.parseFiltersToQuery() + this.state.objectIdFilters} />
        <ComposedComponent
          addFilter={this.addFilter}
          removeFilter={this.removeFilter}
          updateFilter={this.updateFilter}
          changeIndex={this.changeIndex}
          forceRefresh={this.forceRefresh}
          addObjectIdFilters={this.addObjectIdFilters}
          {...this.props}
        />
      </InstantSearch>
    )
  }
}

export default withAlgolia



