import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { pick, compose } from 'ramda'
import JobOfferSearchList from 'Candidate/views/JobsView/components/JobOfferSearchList'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import withTranslations from 'Shared/hoc/withTranslations'
import INDEX_NAME, { SEARCH_ITEMS_PER_PAGE_MOBILE_COUNT, INFINITE_SEARCH_ITEMS_PER_PAGE_COUNT } from 'Shared/constants/Algolia'
import { ISOcodes } from 'Shared/constants/i18n'
import { Roles } from 'Shared/constants/User'
import LanguageService from 'Shared/services/LanguageService'
import { humanize } from 'Shared/helpers/string'
import { fetchUserEducationDetails } from 'Shared/ducks/education'
import { getLastCandidateMajor } from 'Shared/selectors/education'
import { getUrlToSearchState } from 'Shared/selectors/url'
import { fetchJobOffersBookmarksForCandidate } from 'Shared/ducks/jobOffersBookmarks'
import { fetchCandidateApplicationsIfNeeded } from 'Shared/ducks/jobOfferApplications'
import { isMobile } from 'Shared/helpers/mobile'
import withScripts from 'TrackingScripts'
import { getRefinementListValues, getQueryValue } from 'Shared/helpers/Algolia'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { fetchCountriesIfNeeded } from 'Shared/ducks/resources'
import { isCandidateProfileIncomplete, getFirstProfileUncompleteStep } from 'Shared/selectors/user'

// FIXME consider to omit defining new objects in connector

const mapStateToProps = (state, ownProps) => {
  const searchState = getUrlToSearchState(ownProps.location.search, true)
  const { role } = state.user
  let indexName

  switch (true) {
    case role === Roles.candidate.name:
      indexName = searchState.sort || INDEX_NAME.JOB_OFFERS_RECENT
      break
    default:
      indexName = INDEX_NAME.JOB_OFFERS_RECENT
      break
  }

  const pickedProps = pick(['jobType', 'city', 'department'], ownProps.match.params)
  const refinementFromParams = Object.keys(pickedProps)
    .reduce((prev, curr) => {
      prev[curr] = [humanize(pickedProps[curr], true)]
      return prev
    }, {})

  const searchStateValues = {
    ...getRefinementListValues(searchState),
    ...getQueryValue(searchState)
  }

  return ({
    filters: { languageIsoCode: `languageIsoCode:${ISOcodes[LanguageService.defaultLanguage]}` },
    indexName,
    isCandidate: role === Roles.candidate.name,
    countries: state.resources.countries.entries,
    searchState: {
      refinementList: { ...refinementFromParams, ...ownProps.jobSearchData.refinementList },
      ...searchState
    },
    role,
    candidateId: state.user.details.id,
    lastCandidateMajor: getLastCandidateMajor(state.user.details.id)(state),
    configureProps: { hitsPerPage: isMobile() ? SEARCH_ITEMS_PER_PAGE_MOBILE_COUNT : INFINITE_SEARCH_ITEMS_PER_PAGE_COUNT },
    currentLocation: state.geolocation.currentLocation,
    searchStateValues,
    isCandidateProfileIncomplete: isCandidateProfileIncomplete(state),
    firstIncompleteStep: getFirstProfileUncompleteStep(state)
  })
}

const mapDispatchToProps = (dispatch) => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
  fetchCountriesIfNeeded: () => (dispatch(fetchCountriesIfNeeded())),
  fetchUserEducationDetails: () => dispatch(fetchUserEducationDetails()),
  fetchJobOffersBookmarksForCandidate: (candidateId) => dispatch(fetchJobOffersBookmarksForCandidate(candidateId)),
  fetchCandidateApplicationsIfNeeded: (candidateId) => dispatch(fetchCandidateApplicationsIfNeeded(candidateId))
})

export default compose(
  withRouter,
  withScripts,
  connect(mapStateToProps, mapDispatchToProps),
  withTranslations,
  withAlgolia
)(JobOfferSearchList)



