import React, { Component } from 'react'
import PropTypes from 'prop-types'
import * as R from 'ramda'
import { t } from 'i18next'
import cx from 'classnames'
import { capitalizeFirst } from 'Shared/helpers/string'
import { connectRefinementList } from 'react-instantsearch-dom'
import withIncreasedRefinementLimit from 'Algolia/hoc/withIncreasedRefinementLimit'
import { isArabicLayout } from 'Shared/helpers/layout'
import './MultiSelectRefinementList.scss'

class MultiSelectRefinementList extends Component {
  state = {
    searchQuery: '',
    isOpen: false
  }

  onSearchInputChange = event => this.setState({ searchQuery: event.target.value })

  currentRefinementLength = () => {
    const { currentRefinement } = this.props
    const count = currentRefinement.length
    const getCounter = count => <span className='multi-select-refinement-list-mobile__category-counter'>{count}</span>

    return R.ifElse(
      R.anyPass([R.gt(0), R.equals(0)]),
      R.always(''),
      getCounter
    )(count)
  }

  onCheckboxClick = value => () => this.props.refine(value)

  refinementListItem = (item, index) => (
    <div key={item.label} >
      <label className='multi-select-refinement-list-mobile__checkbox-label'>
        <span>{item.label} </span>
        <span className='search-box__filters-count' dangerouslySetInnerHTML={{ __html: `(${item.count})${isArabicLayout ? '&#x200e;' : '&#x200f;'}` }} />
        <input
          className='multi-select-refinement-list-mobile__checkbox'
          type='checkbox'
          onChange={this.onCheckboxClick(item.value)}
          checked={item.isRefined}
        />
        <span className='multi-select-refinement-list-mobile__checkmark' />
      </label>
    </div>
  )

  normalizeItemLabel = item => R.assoc(
    'label',
    R.pipe(
      R.prop('label'),
      this.props.normalizeLabel
    )(item),
    item
  )

  refinementListItems = () => {
    const { handleCustomFilter, handleCustomSort } = this.props

    return R.pipe(
      R.map(this.normalizeItemLabel),
      R.filter(
        R.pipe(
          R.propOr('', 'label'),
          R.toString,
          R.toLower,
          R.includes(this.state.searchQuery.toLowerCase())
        )
      ),
      handleCustomFilter,
      R.sortBy(R.prop('label')),
      handleCustomSort,
      R.map(this.refinementListItem)
    )(this.props.items)
  }

  refinementListItemsSearch = () => {
    return (
      <div className='multi-select-refinement-list-mobile__input-wrapper'>
        <i className='icon-im-search' />
        <input
          className='multi-select-refinement-list-mobile__input'
          type='search'
          onChange={this.onSearchInputChange}
          placeholder={capitalizeFirst(`${t('common.search')} ${this.props.name}`)}
        />
      </div>
    )
  }

  openFilter = () => this.setState({ isOpen: true })
  closeFilter = () => this.setState({ isOpen: false })

  render () {
    const { name } = this.props
    const itemsClassName = cx({
      'multi-select-refinement-list-mobile__list': true,
      'multi-select-refinement-list-mobile__list--visible': this.state.isOpen,
      'multi-select-refinement-list-mobile__list--hidden': !this.state.isOpen
    })

    return (
      <div className='multi-select-refinement-list-mobile__container parsing--hidden'>
        <div className='multi-select-refinement-list-mobile__category' onClick={this.openFilter}>
          <div><span>{name}</span>{this.currentRefinementLength()}</div>
          <i className='icon-im-caret-right' />
        </div>
        <div className={itemsClassName}>
          <div className='multi-select-refinement-list-mobile__top-nav'>
            <div className='multi-select-refinement-list-mobile__title'>{name}</div>
            <div className='multi-select-refinement-list-mobile__top-nav-btns'>
              <div onClick={this.closeFilter} className='icon-im-arrow-forward multi-select-refinement-list-mobile__back' />
              <div onClick={this.closeFilter} className='multi-select-refinement-list-mobile__done'>{t('common.done')}</div>
            </div>
          </div>
          <div className='multi-select-refinement-list-mobile__search'>
            {this.refinementListItemsSearch()}
          </div>
          <div className='multi-select-refinement-list-mobile__items'>
            {this.refinementListItems()}
          </div>
        </div>
      </div>
    )
  }
}

MultiSelectRefinementList.propTypes = {
  name: PropTypes.string.isRequired
}

MultiSelectRefinementList.defaultProps = {
  withRefinementSearch: true,
  handleCustomFilter: items => items,
  normalizeLabel: label => label,
  handleCustomSort: items => items,
  items: []
}

export default withIncreasedRefinementLimit(connectRefinementList(MultiSelectRefinementList))



