import JobsViewWeb from './JobsView'
import JobsViewMobile from './JobsView.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const JobsView = (props) => props.isMobile ? <JobsViewMobile {...props} /> : <JobsViewWeb {...props} />

export default withDeviceWidthDetector(JobsView)



