import React, { Component } from 'react'
import PropTypes from 'prop-types'
import ReactModal from 'react-modal'
import Button, { PrimaryButton } from 'Shared/components/Button'
import cx from 'classnames'
import { t } from 'i18next'
import './WindowModal.scss'

class WindowModal extends Component {
  render () {
    const {
      children,
      title,
      show,
      showCancel,
      showSubmit,
      additionalButtons,
      handleCancel,
      handleSubmit,
      cancelText,
      submitText,
      disableSubmit,
      saveInDom
    } = this.props
    // saveInDom is added because Algolia filters needs to be in DOM to have the search triggered //Sandra 02.04.2019

    const modalClass = cx({
      'window-modal': true,
      'parsing--hidden': true,
      'window-modal--show': saveInDom && show,
      'window-modal--hide': saveInDom && !show
    })

    const portalClass = cx({
      'window-modal__portal': true,
      'window-modal--show': saveInDom && show,
      'window-modal--hide': saveInDom && !show
    })

    return (
      <ReactModal
        isOpen={saveInDom ? true : show}
        contentLabel='Modal'
        closeTimeoutMS={300}
        onRequestClose={() => {}}
        className={modalClass}
        portalClassName={portalClass}
      >
        <div className='window-modal__heading-container'>
          <div className='container container--large'>
            <div className='window-modal__heading'>
              <div className='window-modal__title'>{title}</div>
              <div className='window-modal__btns'>
                <div className='window-modal__additional-btns'>{ additionalButtons }</div>
                { showCancel && <Button type='button' onClick={handleCancel}>{cancelText}</Button> }
                { showSubmit && <PrimaryButton type='button' disabled={disableSubmit} onClick={handleSubmit}>{submitText}</PrimaryButton> }
              </div>
            </div>
          </div>
        </div>
        <div className='window-modal__content-container'>
          <div className='container container--large'>
            {children}
          </div>
        </div>
      </ReactModal>
    )
  }
}

WindowModal.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
  cancelText: PropTypes.string,
  submitText: PropTypes.string,
  handleCancel: PropTypes.func,
  handleSubmit: PropTypes.func,
  show: PropTypes.bool,
  showCancel: PropTypes.bool,
  showSubmit: PropTypes.bool,
  loading: PropTypes.bool,
  disableSubmit: PropTypes.bool
}

WindowModal.defaultProps = {
  show: false,
  showCancel: true,
  showSubmit: true,
  closeOnSubmit: true,
  closeOnCancel: true,
  additionalButtons: '',
  cancelText: t('common.cancel'),
  submitText: t('common.submit')
}

export default WindowModal



