import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connectSearchBox } from 'react-instantsearch-dom'
import { trimLeadingSpaces } from 'Shared/helpers/string'
import { DEFAULT_DEBOUNCE_DELAY } from 'Shared/constants/Global'
import debounce from 'lodash/debounce'
import { t } from 'i18next'
import './SearchInput.scss'
import * as R from 'ramda'
import qs from 'query-string'
import cx from 'classnames'

class SearchInput extends Component {
  getQueryFromPath = (props = this.props) => R.pipe(
    R.pathOr('', ['location', 'search']),
    qs.parse,
    R.propOr('', 'query')
  )(props)

  state = {
    value: this.getQueryFromPath()
  }

  componentDidUpdate (prevProps) {
    if (R.has('savedSearchId', this.props.match.params) && this.props.match.params.savedSearchId !== prevProps.match.params.savedSearchId) {
      this.setState({ value: this.getQueryFromPath() })
    }

    if (this.getQueryFromPath() !== this.getQueryFromPath(prevProps)) {
      this.setState({ value: this.getQueryFromPath() })
    }
  }

  debounceRefine = debounce(this.props.refine, DEFAULT_DEBOUNCE_DELAY)
  onInputChange = event => {
    const { onChange } = this.props
    const value = trimLeadingSpaces(event.target.value)
    onChange(value)
    this.setState({ value })
    this.handleSearch(value)
  }

  handleSearch = value => {
    const { withDebounce, refine } = this.props
    return withDebounce ? this.debounceRefine(value) : refine(value)
  }

  render () {
    const inputClass = cx({
      'search-input__input-container': true,
      'parsing--hidden': true,
      'search-input__input-container--loaded': this.props.isLoaded
    })

    return (
      <div className={inputClass}>
        <i className='search-input__icon ion-md-search' />
        <input id='search-box-input' value={this.state.value} onChange={this.onInputChange} placeholder={this.props.placeholder} className='search-input__input' type='text' />
        {this.props.additionalButton}
      </div>
    )
  }
}

SearchInput.defaultProps = {
  refine: () => {},
  withDebounce: false,
  onChange: () => {},
  additionalButton: '',
  placeholder: t('common.search')
}

SearchInput.propTypes = {
  refine: PropTypes.func.isRequired,
  withDebounce: PropTypes.bool.isRequired,
  onChange: PropTypes.func
}

export default connectSearchBox(withRouter(SearchInput))



