import React, { Component } from 'react'
import { connectRefinementList } from 'react-instantsearch-dom'
import withIncreasedRefinementLimit from 'Algolia/hoc/withIncreasedRefinementLimit'
import RadioWrapper from 'Shared/components/FormElements/RadioWrapper/RadioWrapper'
import Radio from 'Shared/components/FormElements/Radio/Radio'
import './RadioSelectRefinementList.scss'
import * as R from 'ramda'
import { ConfirmPopup } from 'Shared/components/Popup'
import cx from 'classnames'

const mapWithIndex = R.addIndex(R.map)

class RadioSelectRefinementList extends Component {
  state = {
    value: this.props.initialValue
  }

  handleRefine = () => {
    return this.props.handleRefine(this.state.value, this.props.refine)
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.state.value !== prevState.value) {
      this.handleRefine()
    }

    if (this.props.initialValue !== prevProps.initialValue) {
      this.setState({ value: this.props.initialValue })
    }

    if (R.isEmpty(this.props.currentRefinement) && R.not(R.isEmpty(prevProps.currentRefinement))) {
      this.setState({ value: this.props.initialValue })
    }
  }

  radioOption = (option, index) => {
    const radioProps = {
      input: {
        onChange: () => this.setState({ value: option.value }),
        value: this.state.value
      },
      meta: { touched: false, error: false, submitting: false }
    }

    return <Radio
      key={`radio-select-filter-${option.label}-${index}`}
      {...radioProps}
      radioButtonValue={option.value}
      text={option.label}
    />
  }

  radioOptionsList = () => {
    const options = this.props.getOptions(this.props.items)

    return mapWithIndex(
      this.radioOption
    )(options)
  }

  filterList = () => (
    <div className='radio-select-filters-list__items-container parsing--hidden'>
      <div className='radio-select-filters-list__items-list-container'>
        <RadioWrapper optional name={this.props.name} className='radio-select-filters-list__radio-wrapper'>
          {this.radioOptionsList()}
        </RadioWrapper>
      </div>
    </div>
  )

  filterItem = () => {
    const { name, isLoaded } = this.props
    const refinementClassName = cx({
      'radio-select-filters-list': true,
      'radio-select-filters-list--loaded': isLoaded
    })

    return (
      <div className={refinementClassName}>
        {name}
        {this.state.value !== this.props.initialValue && <div className='radio-select-filters-list__count'>{1}</div>}
      </div>
    )
  }

  render () {
    const { withPopup } = this.props

    return withPopup ? (
      <ConfirmPopup
        position='bottom'
        align='left'
        triggerButton={this.filterItem()}
      >
        {this.filterList()}
      </ConfirmPopup>
    ) : (
      this.filterList()
    )
  }
}

export default withIncreasedRefinementLimit(connectRefinementList(RadioSelectRefinementList))



