import React, { PureComponent } from 'react'
import { withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations/index'
import JobOfferCardAlgoliaMobileConnector from 'Candidate/connectors/JobOfferCardAlgolia'
import WithJobOfferEmptyStateWrapper from './withJobOfferEmptyStateWrapper'
import searchImage from 'Shared/assets/images/vectors/search.svg'
import InfiniteScroll from 'Algolia/components/InfiniteScroll'

class JobsEmptyStateListMobile extends PureComponent {
  render () {
    return (
      <div>
        <div className='jobs-view-mobile__list-container'>
          <div className='job-view-mobile--empty'>
            <div className='jobs-view-mobile--empty-image'>
              <img src={searchImage} />
            </div>
            <div>{this.props.t('jobOffersList.emptyStateMobile.title')}</div>
            <div>{this.props.t('jobOffersList.emptyStateMobile.description')}</div>
          </div>
          <div className='jobs-view-mobile--empty-header'>
            {this.props.t('jobOffersList.checkAvailableJobs')}
          </div>
          <div>
            <InfiniteScroll hitComponent={JobOfferCardAlgoliaMobileConnector} />
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(WithJobOfferEmptyStateWrapper(withTranslations(JobsEmptyStateListMobile)))



