import React, { useState, useEffect } from 'react'
import './UpdateSalaryExpectationNotification.scss'
import Notification from 'Shared/components/Notification'
import { Trans } from 'react-i18next'
import Modal from 'Shared/components/Modal'
import { t } from 'i18next'
import SalaryExpectationFormConnector from 'Candidate/connectors/Forms/SalaryExpectationFormFields/SalaryExpectationFormConnector'

function UpdateSalaryExpectationNotification (props) {
  const [isModalVisible, setModalVisibility] = useState(false)

  useEffect(() => {
    if (props.isSalaryExpectationFormSucceeded) {
      setModalVisibility(false)
    }
  }, [props.isSalaryExpectationFormSucceeded])

  const openModal = () => setModalVisibility(true)
  const closeModal = () => setModalVisibility(false)

  const notificationText = () => (
    <Trans i18nKey='notifications.candidate.updateSalaryRange'>
      Please <span onClick={openModal} className='update-salary-expectations__btn'>update your salary</span>expectations so that we can find you jobs that match you best.
    </Trans>
  )

  const manualPaymentModal = () => (
    <Modal
      className='update-salary-expectations__modal'
      title={t('modals.salaryExpectations.title')}
      submitText={t('common.saveChanges')}
      cancelText={t('common.cancel')}
      handleClose={closeModal}
      handleCancel={closeModal}
      handleSubmit={props.submitSalaryExpectationForm}
      closeOnSubmit={false}
      show={isModalVisible}
    >
      <div className='update-salary-expectations__modal-description'>
        {t('modals.salaryExpectations.description')}
      </div>
      <SalaryExpectationFormConnector closeModal={closeModal} />
    </Modal>
  )

  return props.isSalaryExpectationMissing && props.isCandidate ? (
    <div>
      <Notification
        messageText={notificationText()}
        iconClassName='icon-im-alert-circle-outline'
        className='failed-payment-notification'
        warning
      />
      {manualPaymentModal()}
    </div>
  ) : <div />
}

export default UpdateSalaryExpectationNotification



