import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Stats from 'Algolia/components/Stats'
import { connectStats } from 'react-instantsearch-dom'
import withTranslations from 'Shared/hoc/withTranslations/index'
import './SearchResults.scss'
import cx from 'classnames'

// TODO: this component needs refactor. Please keep algolia connectors separately!!!
// please create separate component for algolia Stats and algolia current refinements to avoid nested connects
// <-- nested algolia connectors slows down the application !! //Sandra 03.04.2019

class SearchResults extends PureComponent {
  componentDidMount () {
    // fixme: Temporary crash fix -- search through usages of this component
    this.props.setSearchResults(this.props.nbHits)
  }

  componentDidUpdate (prevProps) {
    if (prevProps.nbHits !== this.props.nbHits) {
      this.props.setSearchResults(this.props.nbHits)
    }
  }

  get selectedSearchLabel () {
    const { selectedSearchName } = this.props
    return <div className='search-box__container-selected-search-name'>
      <span>{this.props.t('common.for')}&nbsp;</span>
      <span className='search-box__selected-search-name'>{selectedSearchName}</span>
    </div>
  }

  render () {
    const containerClass = cx({
      'search-box__container': true,
      'search-box__container--hidden': this.props.hidden
    })

    return (
      <div className={containerClass}>
        {
          !this.props.hidden && (
            <div>
              <Stats
                hitsLength={this.props.nbHits}
                generateLabel={this.props.generateLabel}
                isDefaultSearch={this.props.isDefaultSearch}
              />
            </div>
          )
        }
        { this.props.selectedSearchName && this.selectedSearchLabel }
      </div>
    )
  }
}

SearchResults.defaultProps = {
  setSearchResults: () => {}
}

SearchResults.propTypes = {
  generateLabel: PropTypes.func,
  selectedSearchName: PropTypes.string,
  hideCurrentRefinements: PropTypes.bool
}

export default withTranslations(connectStats(SearchResults))



