import React, { Component } from 'react'
import PropTypes from 'prop-types'
import * as R from 'ramda'
import { t } from 'i18next'
import cx from 'classnames'
import { capitalizeFirst } from 'Shared/helpers/string'
import { connectRefinementList } from 'react-instantsearch-dom'
import withIncreasedRefinementLimit from 'Algolia/hoc/withIncreasedRefinementLimit'
import { ConfirmPopup } from 'Shared/components/Popup'
import { isArabicLayout } from 'Shared/helpers/layout'
import { JustCheckbox } from 'Shared/components/FormElements/Checkbox'
import './MultiSelectRefinementList.scss'

const mapWithIndex = R.addIndex(R.map)

class MultiSelectRefinementList extends Component {
  state = {
    searchQuery: ''
  }

  onPopupClose = () => {
    this.props.withAlgoliaSearch && this.props.searchForItems('')
    return this.setState({ searchQuery: '' })
  }

  onSearchInputChange = event => {
    this.props.withAlgoliaSearch && this.props.searchForItems(event.target.value)
    return this.setState({ searchQuery: event.target.value })
  }

  currentRefinementLength = () => {
    const { currentRefinement } = this.props
    const count = currentRefinement.length
    const getCounter = count => <div className='multi-select-refinement-list__count'>{count}</div>

    return R.ifElse(
      R.anyPass([R.gt(0), R.equals(0)]),
      R.always(''),
      getCounter
    )(count)
  }

  refinementItem = () => {
    const { name, isLoaded } = this.props
    const refinementClassName = cx({
      'multi-select-refinement-list': true,
      'multi-select-refinement-list--loaded': isLoaded
    })

    return (
      <div className={refinementClassName}>
        {name}
        {this.currentRefinementLength()}
      </div>
    )
  }

  onCheckboxClick = value => () => {
    const valuesToString = R.map(val => val.toString())(value)
    const hasMoreThatOneValue = R.pipe(
      R.length,
      R.gt(2),
      R.not
    )

    const hasAllTheSameValues = R.pipe(
      R.uniq,
      R.length,
      R.equals(1)
    )

    const values = R.ifElse(
      R.allPass([hasMoreThatOneValue, hasAllTheSameValues]),
      R.always([]),
      R.uniq
    )(valuesToString)

    return this.props.refine(values)
  }

  refinementListItem = (item, index) => (
    <div className='multi-select-refinement-list__item' key={`${this.props.name}-${item.label}-${index}`}>
      <JustCheckbox onClick={this.onCheckboxClick(item.value)} checked={item.isRefined}>
        <span>{this.props.labelWithCapitalizeFirst ? capitalizeFirst(item.label.toString()) : item.label.toString()}&nbsp;</span>
        <span
          dangerouslySetInnerHTML={{ __html: `(${item.count})${isArabicLayout ? '&#x200e;' : '&#x200f;'}` }}
        />
      </JustCheckbox>
    </div>
  )

  normalizeItemLabel = item => R.assoc(
    'label',
    R.pipe(
      R.prop('label'),
      this.props.normalizeLabel
    )(item),
    item
  )

  refinementListItems = () => {
    const { handleCustomFilter, handleCustomSort } = this.props

    return R.pipe(
      R.map(this.normalizeItemLabel),
      R.filter(
        R.pipe(
          R.propOr('', 'label'),
          R.toString,
          R.toLower,
          R.includes(this.state.searchQuery.toLowerCase())
        )
      ),
      handleCustomFilter,
      R.sortBy(R.prop('label')),
      handleCustomSort,
      mapWithIndex(this.refinementListItem)
    )(this.props.items)
  }

  refinementListItemsSearch = () => {
    return (
      <input
        className='multi-select-refinement-list__items-search'
        type='search'
        onChange={this.onSearchInputChange}
        placeholder={capitalizeFirst(`${t('common.search')} ${this.props.name}`)}
      />
    )
  }

  refinementList = () => (
    <div className='multi-select-refinement-list__items-container parsing--hidden'>
      {
        this.props.withRefinementSearch && (
          <div className='multi-select-refinement-list__items-search-container'>
            {this.refinementListItemsSearch()}
          </div>
        )
      }
      <div className='multi-select-refinement-list__items-list-container'>
        <div className='multi-select-refinement-list__items-list-container-inner'>
          {this.refinementListItems()}
        </div>
      </div>
    </div>
  )

  render () {
    const { withPopup } = this.props

    return withPopup ? (
      <ConfirmPopup
        onClose={this.onPopupClose}
        position='bottom'
        align='left'
        triggerButton={this.refinementItem()}
      >
        {this.refinementList()}
      </ConfirmPopup>
    ) : (
      this.refinementList()
    )
  }
}

MultiSelectRefinementList.propTypes = {
  name: PropTypes.string.isRequired
}

MultiSelectRefinementList.defaultProps = {
  withRefinementSearch: true,
  withAlgoliaSearch: true,
  handleCustomFilter: items => items,
  normalizeLabel: label => label,
  handleCustomSort: items => items,
  labelWithCapitalizeFirst: false
}

export default withIncreasedRefinementLimit(connectRefinementList(MultiSelectRefinementList))



