import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connectSearchBox } from 'react-instantsearch-dom'
import { trimLeadingSpaces } from 'Shared/helpers/string'
import { DEFAULT_DEBOUNCE_DELAY } from 'Shared/constants/Global'
import debounce from 'lodash/debounce'
import './SearchInput.scss'
import * as R from 'ramda'
import qs from 'query-string'

class SearchInput extends Component {
  getQueryFromPath = (props = this.props) => R.pipe(
    R.pathOr('', ['location', 'search']),
    qs.parse,
    R.propOr('', 'query')
  )(props)

  state = {
    value: this.getQueryFromPath()
  }

  debounceRefine = debounce(this.props.refine, DEFAULT_DEBOUNCE_DELAY)
  onInputChange = event => {
    const { onChange } = this.props
    const value = trimLeadingSpaces(event.target.value)
    onChange(value)
    this.setState({ value })
    this.handleSearch(value)
  }

  handleSearch = value => {
    const { withDebounce, refine } = this.props
    return withDebounce ? this.debounceRefine(value) : refine(value)
  }

  removeSearchQuery = () => {
    this.setState({ value: '' })
    this.props.refine('')
  }

  render () {
    return (
      <div className='search-input-mobile__input-container parsing--hidden'>
        <i className='search-input-mobile__icon ion-md-search' />
        <input value={this.state.value} onChange={this.onInputChange} className='search-input-mobile__input' type='text' />
        {this.state.value.length > 0 &&
        <i onClick={this.removeSearchQuery} className='search-input-mobile__icon ion-md-close-circle' />}
      </div>
    )
  }
}

SearchInput.defaultProps = {
  refine: () => {},
  withDebounce: false,
  onChange: () => {}
}

SearchInput.propTypes = {
  refine: PropTypes.func.isRequired,
  withDebounce: PropTypes.bool.isRequired,
  onChange: PropTypes.func
}

export default connectSearchBox(withRouter(SearchInput))



