import React, { useEffect } from 'react'
import { Form } from 'redux-form'
import SalaryExpectationFields from 'Candidate/connectors/Forms/SalaryExpectationFormFields/SalaryExpectationFormFieldsConnetor'

function SalaryExpectationForm (props) {
  useEffect(() => {
    props.fetchCurrencies()
  }, [])

  return (
    <Form onSubmit={props.handleSubmit}>
      <SalaryExpectationFields {...props} />
    </Form>
  )
}

export default SalaryExpectationForm



