import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import WindowModal from 'Shared/components/WindowModal'
import { t } from 'i18next'
import MultiSelectRefinementList from 'Algolia/components/MultiSelectRefinementList'
import JobOfferDatePostedRefinementList from 'Algolia/components/JobOfferDatePostedRefinementList'
import EasyApplyToggle from 'Algolia/components/EasyApplyToggle'
import Label from 'Shared/components/FormElements/Label/Label'
import { sortJobDurationFilterItems } from 'Shared/helpers/filters'
import './JobsMoreSearchFilters.scss'
import { transformItems } from 'Shared/helpers/Algolia'

class JobsMoreSearchFilters extends Component {
  handleCancel = () => {
    this.props.history.push(`${this.props.location.pathname}${this.props.currentSearch}`)
    this.props.hideMoreFilters()
  }

  handleSave = () => {
    this.props.hideMoreFilters()
  }

  render () {
    return (
      <WindowModal
        title={t('common.allFilters')}
        additionalButtons={this.props.additionalButtons}
        handleCancel={this.handleCancel}
        handleSubmit={this.handleSave}
        submitText={t('common.apply')}
        saveInDom
        show={this.props.showMoreFilters}
      >
        <div className='jobs-search-filters__filters-container'>
          <JobOfferDatePostedRefinementList />
          <MultiSelectRefinementList
            attribute='city'
            name={t('searchAttributes.city')}
            labelWithCapitalizeFirst={false}
            withPopup
          />
          <MultiSelectRefinementList
            attribute='jobType'
            name={t('searchAttributes.jobType')}
            withPopup
          />
          <MultiSelectRefinementList
            attribute='department'
            name={t('searchAttributes.category')}
            withPopup
          />
          <MultiSelectRefinementList
            attribute='industry'
            name={t('searchAttributes.companyType')}
            withPopup
          />
          <EasyApplyToggle />
        </div>
        <div className='jobs-more-filters__filters-row'>
          <div className='jobs-more-filters__refinement-container'>
            <Label>{t('searchAttributes.country')}</Label>
            <MultiSelectRefinementList
              attribute='country'
              name={t('searchAttributes.country')}
              labelWithCapitalizeFirst={false}
              withAlgoliaSearch={false}
              transformItems={transformItems.country(this.props.countries)}
            />
          </div>
          <div className='jobs-more-filters__refinement-container'>
            <Label>{t('searchAttributes.commitment')}</Label>
            <MultiSelectRefinementList
              attribute='commitment'
              name={t('searchAttributes.commitment')}
            />
          </div>
          <div className='jobs-more-filters__refinement-container'>
            <Label>{t('searchAttributes.duration')}</Label>
            <MultiSelectRefinementList
              attribute='duration'
              name={t('searchAttributes.duration')}
              handleCustomSort={sortJobDurationFilterItems}
            />
          </div>
          <div className='jobs-more-filters__refinement-container' />
        </div>
      </WindowModal>
    )
  }
}

export default withRouter(JobsMoreSearchFilters)



