import React from 'react'
import pure from 'recompose/pure'
import { t } from 'i18next'
import PropTypes from 'prop-types'
import './Stats.scss'

const Stats = props => {
  const generateLabel = () => {
    const showHitsLength = props.isDefaultSearch ? '' : `(${props.hitsLength})`

    return props.generateLabel ? props.generateLabel(props.hitsLength) : `${t('common.searchResults')} ${showHitsLength}`
  }

  return (
    <div className='c-algolia-stats'>
      {generateLabel()}
    </div>
  )
}

Stats.propTypes = {
  hitsLength: PropTypes.number,
  generateLabel: PropTypes.func,
  isDefaultSearch: PropTypes.bool
}

export default pure(Stats)



