import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connectRange } from 'react-instantsearch-dom'
import * as R from 'ramda'
import { t } from 'i18next'
import { ConfirmPopup } from 'Shared/components/Popup'
import RadioWrapper from 'Shared/components/FormElements/RadioWrapper/RadioWrapper'
import Radio from 'Shared/components/FormElements/Radio/Radio'
import cx from 'classnames'
import './RangeSelectRefinementList.scss'

class RangeSelectRefinementList extends Component {
  state = {
    value: ''
  }

  componentDidMount () {
    this.setState({ value: this.props.getInitialValue(this.props.currentRefinement) })
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.location.search !== this.props.location.search) {
      this.setState({ value: this.props.getInitialValue(this.props.currentRefinement) })
    }
  }

  currentRefinementLength = () => {
    const { defaultValue } = this.props
    const getCounter = () => <div className='range-select-refinement-list__count'>{1}</div>

    return R.ifElse(
      R.equals(defaultValue),
      R.always(''),
      getCounter
    )(this.state.value)
  }

  refinementItem = () => {
    const { name, isLoaded } = this.props
    const rangeSelectClass = cx({
      'range-select-refinement-list': true,
      'range-select-refinement-list--loaded': isLoaded
    })

    return (
      <div className={rangeSelectClass}>
        {name}
        {this.currentRefinementLength()}
      </div>
    )
  }

  onSelectRadio = value => {
    this.setState({ value })
    return this.props.refine(this.props.refineValues(value))
  }

  refinementListItem = item => {
    const { attribute } = this.props
    const radioProps = {
      input: {
        onChange: this.onSelectRadio,
        value: this.state.value
      },
      meta: { touched: false, error: false, submitting: false }
    }

    return (
      <Radio
        key={`range-${attribute}-radio-${item.inputValue}`}
        {...radioProps}
        radioButtonValue={item.inputValue}
        text={t(item.translationPath)}
      />
    )
  }

  refinementListItems = () => {
    return R.map(this.refinementListItem)(this.props.options)
  }

  render () {
    return (
      <ConfirmPopup
        position='bottom'
        align='left'
        triggerButton={this.refinementItem()}
      >
        <div className='range-select-refinement-list__items-container parsing--hidden'>
          <RadioWrapper optional name={this.props.attribute} className='range-select-refinement-list__radio-wrapper'>
            {this.refinementListItems()}
          </RadioWrapper>
        </div>
      </ConfirmPopup>
    )
  }
}

export default connectRange(withRouter(RangeSelectRefinementList))



