import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import { SearchResults } from 'Algolia/components/SearchResult/index'
import Container from 'Shared/components/Container/index'
import WithJobOfferSearchListWrapper from './withJobOfferSearchListWrapper'
import JobOfferCardAlgoliaConnector from 'Candidate/connectors/JobOfferCardAlgolia'
import { isNotNil, isNotEmpty, notEquals } from 'Shared/helpers/Ramda'
import { transformLabels } from 'Shared/helpers/Algolia'
import * as R from 'ramda'
import JobsViewListLoader from 'Candidate/views/JobsView/components/JobsViewListLoader/JobsViewListLoader'
import JobsViewBox from 'Candidate/components/JobsViewBox'
import JobsSearchFiltersConnector from 'Candidate/connectors/JobsSearchFilters/JobsSearchFiltersConnector'
import HitsLoader from 'Algolia/components/HitsLoader'
import InfiniteScroll from 'Algolia/components/InfiniteScroll'
import { Roles } from 'Shared/constants/User'
import { t } from 'i18next'
import { Trans } from 'react-i18next'
import Notification from 'Shared/components/Notification/index'
import OlivPlusBannerConnector from 'Candidate/connectors/OlivPlus/OlivPlusBannerConnector'
import UpdateSalaryExpectationNotificationConnector from 'Candidate/connectors/UpdateSalaryExpectationNotification/UpdateSalaryExpectationNotificationConnector'

class JobOfferSearchList extends Component {
  componentWillReceiveProps (nextProps) {
    R.when(
      R.allPass([
        R.propSatisfies(isNotNil, 'candidateId'),
        R.propSatisfies(isNotEmpty, 'searchStateValues'),
        R.propSatisfies(notEquals(this.props.searchStateValues), 'searchStateValues')
      ]),
      payload => {
        this.props.isCandidate && this.props.manualAmplitudeScripts.searchForJobs(payload.candidateId, R.prop('searchStateValues', payload))
      }
    )(nextProps)
  }

  transformLabelsConfig = {
    'country': item => {
      return {
        ...item,
        items: item.items.map((el, index) =>
          ({ label: R.pathOr(index, ['countries', el.label, 'country'], this.props), value: el.value })
        )
      }
    }
  }

  transformedLabels = (refinedItems) => {
    return transformLabels(refinedItems, ['hasVideoCv', 'rating'], this.transformLabelsConfig)
  }

  profileCompletionNotification = () => {
    const { isCandidateProfileIncomplete, firstIncompleteStep } = this.props
    const notificationText = () => (
      <Trans i18nKey='notifications.candidate.searchJobsWithIncompleteProfile'>
        <Link to={`/candidate/profile/complete/${firstIncompleteStep}`} className='job-offers-view__notification-link'>Complete</Link> your profile to apply to jobs.
      </Trans>
    )

    return isCandidateProfileIncomplete && (
      <Notification
        messageText={notificationText()}
        iconClassName='icon-im-alert-circle-outline'
        warning
      />
    )
  }

  render () {
    const { searchResults, role } = this.props
    return (
      <div>
        <JobsViewBox>
          <JobsSearchFiltersConnector jobSearchData={this.props.jobSearchData} resetJobSearchData={this.props.resetJobSearchData} />
        </JobsViewBox>
        <Container>
          {this.props.pageHeader}
          <UpdateSalaryExpectationNotificationConnector />
          {this.profileCompletionNotification()}
          <OlivPlusBannerConnector />
          <div className='job-offers-view__search-result columns'>
            <div className='column col-sm-12'>
              <SearchResults
                transformItems={this.transformedLabels}
                setSearchResults={this.props.setSearchResults}
                isDefaultSearch={this.props.isDefaultSearch}
                hideCurrentRefinements
              />
            </div>
            <div className='column col-sm-12'>
              <div className='job-offers-view__relevancy'>
                {
                  role === Roles.candidate.name && (
                    <Link to='/jobs/preferences' className='job-offers-view__preferences'>
                      {t('common.careerPreferences')}
                    </Link>
                  )
                }
                {this.props.sortMenu}
              </div>
            </div>
          </div>
          <div className='job-offers-view__container'>
            <div>
              <HitsLoader loader={<JobsViewListLoader />} />
              { searchResults !== 0 && <InfiniteScroll hitComponent={JobOfferCardAlgoliaConnector} /> }
            </div>
          </div>
        </Container>
      </div>
    )
  }
}

JobOfferSearchList.propTypes = {
  searchStateValues: PropTypes.object.isRequired
}

export default WithJobOfferSearchListWrapper(JobOfferSearchList)



