import { getFormValues, reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import SalaryExpectationsForm from 'Candidate/forms/SalaryExpectationFields/SalaryExpectationForm'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import { prepareSalaryRangeValue, getSalaryRangeValue } from 'Shared/helpers/salary'
import { SALARY_RANGE } from 'Shared/constants/salaryRange'
import { fetchCurrenciesIfNeeded } from 'Shared/ducks/resources'
import { getCurrency } from 'Shared/selectors/resources'
import { getSalaryRange } from 'Shared/selectors/user'
import { pathOr } from 'ramda'

export const formName = 'SalaryExpectationsForm'

let Form = reduxForm({
  form: formName,
  onSubmit: async (values, dispatch) => {
    return dispatch(updateCandidate({ salaryRange: prepareSalaryRangeValue(values) }, true))
  }
})(SalaryExpectationsForm)

const mapStateToProps = state => {
  const selectedRange = getSalaryRange(state)
  const userCurrencyId = pathOr('', ['user', 'details', 'currencyId'], state)

  return ({
    selectedCurrency: getCurrency(state, userCurrencyId),
    selectedValues: getFormValues(formName)(state),
    initialValues: {
      graduateWage: selectedRange || SALARY_RANGE.graduateWage1,
      salaryRange: getSalaryRangeValue(selectedRange) || SALARY_RANGE.notSure
    }
  })
}

const mapDispatchToProps = dispatch => ({
  fetchCurrencies: () => dispatch(fetchCurrenciesIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



