import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JobsSearchFilters from 'Candidate/components/JobsSearchFilters'
import { getUrlToSearchState } from 'Shared/selectors/url'

const mapStateToProps = (state, ownProps) => {
  const searchState = { ...getUrlToSearchState(ownProps.location.search, true), ...ownProps.jobSearchData }

  return ({
    searchState,
    countries: state.resources.countries.entries
  })
}

export default withRouter(connect(mapStateToProps)(JobsSearchFilters))



