import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connectRefinementList } from 'react-instantsearch-dom'
import withIncreasedRefinementLimit from 'Algolia/hoc/withIncreasedRefinementLimit'
import * as R from 'ramda'
import { t } from 'i18next'
import cx from 'classnames'
import './RadioSelectRefinementList.scss'
import Tooltip from 'Shared/components/Tooltip/Tooltip'

class RadioSelectRefinementListMobile extends Component {
  state = {
    value: this.props.initialValue,
    isOpen: false
  }

  handleRefine = () => {
    return this.props.handleRefine(this.state.value, this.props.refine)
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.state.value !== prevState.value) {
      this.handleRefine()
    }

    if (this.props.initialValue !== prevProps.initialValue) {
      this.setState({ value: this.props.initialValue })
    }

    if (R.isEmpty(this.props.currentRefinement) && R.not(R.isEmpty(prevProps.currentRefinement))) {
      this.setState({ value: this.props.initialValue })
    }
  }

  onSelectRadio = value => () => this.setState({ value })

  refinementListItem = (item) => (
    <div key={item.value} >
      <label className='radio-select-refinement-list-mobile__radio-label'>
        <span>{item.label} </span>
        <input
          className='radio-select-refinement-list-mobile__radio'
          type='radio'
          onChange={this.onSelectRadio(item.value)}
          value={item.value}
          checked={this.state.value === item.value}
        />
        <span className='radio-select-refinement-list-mobile__checkmark' />
      </label>
    </div>
  )

  refinementListItems = () => {
    const options = this.props.getOptions(this.props.items)
    return R.map(this.refinementListItem)(options)
  }

  openFilter = () => this.setState({ isOpen: true })
  closeFilter = () => this.setState({ isOpen: false })

  render () {
    const { name, tooltip } = this.props
    const itemsClassName = cx({
      'radio-select-refinement-list-mobile__list': true,
      'radio-select-refinement-list-mobile__list--visible': this.state.isOpen,
      'radio-select-refinement-list-mobile__list--hidden': !this.state.isOpen
    })

    return (
      <div className='radio-select-refinement-list-mobile__container parsing--hidden'>
        <div className='radio-select-refinement-list-mobile__category' onClick={this.openFilter}>
          <div><span>{name}</span></div>
          <i className='icon-im-caret-right' />
        </div>
        <div className={itemsClassName}>
          <div className='radio-select-refinement-list-mobile__top-nav'>
            <div className='radio-select-refinement-list-mobile__title'>{name}</div>
            <div className='radio-select-refinement-list-mobile__top-nav-btns'>
              <div onClick={this.closeFilter} className='icon-im-arrow-forward radio-select-refinement-list-mobile__back' />
              {tooltip && <div className='radio-select-refinement-list-mobile__top-nav-tooltip'>
                <Tooltip trigger='click' content={tooltip}>
                  <i className='ion-md-information-circle' />
                </Tooltip>
              </div>}
              <div onClick={this.closeFilter} className='radio-select-refinement-list-mobile__done'>{t('common.done')}</div>
            </div>
          </div>
          <div className='radio-select-refinement-list-mobile__items'>
            <fieldset>
              {this.refinementListItems()}
            </fieldset>
          </div>
        </div>
      </div>
    )
  }
}

export default withIncreasedRefinementLimit(connectRefinementList(withRouter(RadioSelectRefinementListMobile)))



